<template>
	<div  class="ConsignmentIndex">
		<header><i class="iconfont icon-xiangzuo1" @click="toIndex"></i><span>{{HeadTitle}}</span><i class="iconfont icon-saoma" @click="scan"></i></header>
		<div class="container">
			<div class="import">
				<span>{{invoiceText}}</span>
				<input  placeholder="请输入内容" @keyup.enter="getFocus('CommodityCode')"  @change="toUppercase('InvoiceNum')"  ref="InvoiceNum"   @focus="g_focus('InvoiceNum')" v-model="InvoiceNum" ></input>
				<i v-if="InvoiceNum" @click="clearInput('InvoiceNum')" class="iconfont icon-cuowu"></i>
			</div>
			<div  class="import">
				<span>商品代码</span>
				<input  placeholder="请输入内容"  @keyup.enter="Submit"  @focus="g_focus('CommodityCode')" @change="toUppercase('CommodityCode')"  ref="CommodityCode"  v-model="CommodityCode"></input>
				<i v-if="CommodityCode" @click="clearInput('CommodityCode')"  class="iconfont icon-cuowu"></i>
				<i class="iconfont icon-sousuo"  @click="toChoiceGoods" ></i>
			</div>
			<div class="g_doubleBtn">
				<div @click="Submit">确定</div>
				<div @click="ToCheckDetails">查看明细</div>
			</div>
		</div>
		<div v-if="tipsHide"  @touchmove.prevent  id="g_tips"   class="g_tips">
			<div>
				<span>错误信息</span>
				<span>{{msg}}</span>
				<p   @click='tipsHideen(inputFocus)'>确定</p>
			</div>
		</div>
	</div>
</template>
<script type="text/javascript">

import $ from "jquery"
	export default{
		name:"ConsignmentIndex",
		data(){
			return{
				InvoiceNum:'',//0000003527   发货单号      0000003058 收货单号
				CommodityCode:'',//1010001000   1010002000   商品代码
				tipsHide:false,//提示框是否出现
				msg:"",//提示信息
				inputFocus:"",//遮罩层点击确认后 input聚焦
				thisPage:"Receipt",
				HeadTitle:"收货序列号采集",//页面标题
				invoiceText:"发货单号",//或 收货单号

			}
		},
		mounted() {
			this.$refs['InvoiceNum'].focus();
			window.QRScanAjax = this.QRScanAjax;
			$("input").attr("maxlength","50");
		},
		created(){

			var that = this;

			// 判断从首页的哪个模块进来的
			if(this.$route.query.name){


				if(this.$route.query.name == "Receipt"){
				// 收货序列号采集

					that.$store.state.IsConsignOrReceipt = "Receipt" ;
					that.thisPage = "Receipt";

				}else if(this.$route.query.name == "Consignment"){
				// 发货序列号采集

					that.$store.state.IsConsignOrReceipt = "Consignment" ;
					that.thisPage = "Consignment";

				}
			}else{
			// 从其他页面返回时 判断
				if(this.$store.state.IsConsignOrReceipt == "Receipt"){
					
					that.thisPage = "Receipt";

				}else if(this.$store.state.IsConsignOrReceipt == "Consignment"){

					that.thisPage = "Consignment";
				}
			}

			if(this.thisPage == "Receipt"){

				that.HeadTitle = "收货序列号采集";
				that.invoiceText = "收货单号";

			}else if(this.thisPage == "Consignment"){

				that.HeadTitle = "发货序列号采集";
				that.invoiceText = "发货单号";
			}


			if(this.$route.query.InvoiceNum){

				this.InvoiceNum = this.$route.query.InvoiceNum;
				this.CommodityCode = this.$route.query.CommodityCode;
			}

		},
		methods:{
			// 接收iOS/android值
			QRScanAjax:function (qrcode){

				var inputName = this.$store.state.ScanQRCodeInputName;
				this[inputName] = qrcode;
				if(inputName == "InvoiceNum") {

					this.getFocus('CommodityCode');
				}else if(inputName == "CommodityCode") {

					this.Submit();
				}
			},
 			//跳转到 发/收 货序列号采集页面
			Submit:function(){
				var that = this;

				if(!this.InvoiceNum ){

					that.tipsShow("CommodityCode");

					that.inputFocus = "InvoiceNum";

					that.msg = "单号不能为空";

					return;
				}else if(!this.CommodityCode ){

					that.tipsShow("CommodityCode")

					that.inputFocus = "CommodityCode";

					that.msg = "商品代码不能为空";

					return;
				}

				if(this.thisPage == "Receipt"){

					var currentUrl = this.HOST + "RFReceiptSerialCollectSku.do";


				}else if(this.thisPage == "Consignment"){

					var currentUrl = this.HOST + "RFSerialCollectSku.do";

				}

				var postData = {
					"OrderKey":this.InvoiceNum,
					"Sku":this.CommodityCode,
				}

				this.httpRequest(currentUrl,postData,function(res){

					if(res.data.data.length){

						if(!res.data.data[0].OrderKey){

							that.tipsShow("CommodityCode")

							that.inputFocus = "InvoiceNum";

							that.msg = "此单号不存在";

							return;
						}else if(!res.data.data[0].Sku){

							that.tipsShow("CommodityCode")

							that.inputFocus = "CommodityCode";

							that.msg = "此单中此商品不存在";

							return;
						}else if(res.data.status == 20){

							that.tipsShow("CommodityCode")

							that.inputFocus = "InvoiceNum";

							that.msg = "此单已经取消, 不需扫描";

							return;

						}else{
							that.$store.state.Consignment_SubmitData = res.data.data;

							//跳转到发货序列号采集页面
							that.$router.push({
								name:"ConsignmentNumCollect",
								query:{
								}
							})
						}
					}else{

						that.tipsShow("CommodityCode")

						that.inputFocus = "InvoiceNum";

						that.msg = "未查询到数据";
					}
				},"InvoiceNum")
		    },
		    // 跳转到 查看明细 页面
		    ToCheckDetails:function(){
				
				var that = this;

				if(!this.InvoiceNum ){
					
					that.tipsShow("InvoiceNum")

					that.inputFocus = "InvoiceNum";

					that.msg = "单号不能为空";

					return;
				}

				if(this.thisPage == "Receipt"){

					var CheckDetailsUrl = this.HOST + "RFReceiptSerialCollectQuery.do";


				}else if(this.thisPage == "Consignment"){

					var CheckDetailsUrl = this.HOST + "RFSerialCollectQuery.do";

				}

				var CheckDetailsData = {
					"OrderKey":this.InvoiceNum,
					"Sku":this.CommodityCode,
				}

				this.httpRequest(CheckDetailsUrl,CheckDetailsData,function(res){

					if(res.data.data.length){

						that.$store.state.Consignment_CheckDetailsData = res.data.data;

						//跳转到 查看明细 页面
						that.$router.push({
							name:"CheckDetails",
							query:{
								InvoiceNum:that.InvoiceNum,//发货单号
								CommodityCode:that.CommodityCode,//商品代码
							}
						})
					}else{

						that.tipsShow("InvoiceNum")

						that.inputFocus = "InvoiceNum";

						that.msg = "未查询到数据";
					}
				},"InvoiceNum")
		    },
		    // 跳转到 选择商品 页面
		    toChoiceGoods:function(){
		    
				var that = this;

				if(!this.InvoiceNum ){
					
					that.tipsShow("InvoiceNum")

					that.inputFocus = "InvoiceNum";

					that.msg = "单号不能为空";

					return;
				}

				if(this.thisPage == "Receipt"){

					var CollectSkuSelUrl = this.HOST + "RFReceiptSerialCollectSkuSel.do";


				}else if(this.thisPage == "Consignment"){

					var CollectSkuSelUrl = this.HOST + "RFSerialCollectSkuSel.do";

				}

				var CollectSkuData = {
					"OrderKey":this.InvoiceNum,
					"Sku":this.CommodityCode,
				}

				this.httpRequest(CollectSkuSelUrl,CollectSkuData,function(res){

					if(res.data.data.length){

						that.$store.state.Consignment_ChoiceData = res.data.data;

						//跳转到 选择商品 页面
						that.$router.push({
							name:"ChoiceGoods",
							query:{
								InvoiceNum:that.InvoiceNum,//发货单号
								CommodityCode:that.CommodityCode,//商品代码
							}
						})

					}else{

						that.tipsShow("InvoiceNum")

						that.inputFocus = "InvoiceNum";

						that.msg = "未查询到数据";
					}
				},"InvoiceNum")
		    }
		}
	}
</script>
<style scoped lang="less">
.ConsignmentIndex{
	&>.container{
		font-size: 30/50rem;
		input::placeholder{
			font-size: 25/50rem;
		}
		input{
			font-size: 25/50rem;
		}
		&>div.import{
			width:100%;
			height: 80/50rem;
			padding: 25/50rem 20/50rem 0;
			box-sizing: border-box;
			position: relative;
			span{
				width: 140/50rem;
				text-align: right;
			}
			input{
				margin-left: 10/50rem;
				width:490/50rem;	
				height:55/50rem;
				padding:0 10/50rem;
			}
			i{
				position:absolute;
				right:90/50rem;
				font-size: 30/50rem;
				top: 36/50rem;
			}
			i.icon-sousuo{
				right:30/50rem;
				font-size: 40/50rem;
				top: 30/50rem;
			}
		}
	}
}
@media screen and (max-width: 330px){
	.ConsignmentIndex{
		&>.container{
			font-size: 38/50rem;
			input::placeholder{
				font-size: 35/50rem;
			}
			input{
				font-size: 35/50rem;
			}
			&>div.import{
				height: 100/50rem;
				line-height: 100/50rem;
				input{
					width:445/50rem;	
					height:65/50rem;
					margin: 0;
				}
				i{
					position:absolute;
					right:120/50rem;
					font-size: 42/50rem;
					top: 26/50rem;
				}
				i.icon-sousuo{
					right:25/50rem;
					font-size:50/50rem;
					top: 28/50rem;
				}
				
			}
		}
	}
}
@media screen and (max-width: 277px){
	.ConsignmentIndex{
		&>.container{
			font-size: 42/50rem;
			input::placeholder{
				font-size: 40/50rem;
			}
			input{
				font-size: 40/50rem;
			}
			&>div.import{
				height: 100/50rem;
				line-height: 100/50rem;
				input{
					width:440/50rem;	
					height:70/50rem;
					margin: 0;
				}
				i{
					position:absolute;
					right:110/50rem;
					font-size: 48/50rem;
					top: 26/50rem;
				}
				i.icon-sousuo{
					right:20/50rem;
					font-size:56/50rem;
					top: 28/50rem;
				}
				
			}
		}
	}
}
</style>


